.profile-container {
    padding: 2rem;
    margin-top: 60px; /* Adjust this value based on your navbar height */
  }
  
  .profile-details {
    margin-bottom: 1rem;
  }
  
  .profile-details p {
    margin: 0.5rem 0;
  }
  
  .profile-form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  
  .form-group {
    display: flex;
    flex-direction: column;
  }
  
  .form-group label {
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
  
  .form-group input {
    padding: 0.5rem;
    font-size: 1rem;
  }
  
  .profile-form button {
    padding: 0.75rem;
    font-size: 1rem;
    background-color: #4CAF50;
    color: white;
    border: none;
    cursor: pointer;
    margin-top: 1rem;
  }
  
  .profile-form button[type="button"] {
    background-color: #f44336;
  }
  
  .profile-form button:hover {
    opacity: 0.8;
  }
  
  .error-message {
    color: red;
  }
  